document.addEventListener('DOMContentLoaded', () => {
  document.querySelectorAll('.product').forEach(product => {
    const slider = product.querySelector('.product__slider');
    const slides = product.querySelectorAll('.product__photo');
    const dots = product.querySelectorAll('.product__dots span');
    const btnNext = product.querySelector('.product__arrow--next');
    const btnPrev = product.querySelector('.product__arrow--prev');

    let index = 0;
    let isScrolling = false;

    // --- helpers ---
    const slideWidth = () => slider.clientWidth;

    const updateDots = () => {
      dots.forEach((dot, i) => {
        dot.classList.toggle('is-active', i === index);
      });
    };

    const goToSlide = (i, smooth = true) => {
      index = Math.max(0, Math.min(i, slides.length - 1));

      slider.scrollTo({
        left: slideWidth() * index,
        behavior: smooth ? 'smooth' : 'auto',
      });

      updateDots();
    };

    // --- arrows ---
    if (btnNext) {
      btnNext.addEventListener('click', () => {
        if (index < slides.length - 1) {
          goToSlide(index + 1);
        }
      });
    }

    if (btnPrev) {
      btnPrev.addEventListener('click', () => {
        if (index > 0) {
          goToSlide(index - 1);
        }
      });
    }

    // --- dots click ---
    dots.forEach((dot, i) => {
      dot.addEventListener('click', () => {
        goToSlide(i);
      });
    });

    // --- swipe / scroll ---
    slider.addEventListener('scroll', () => {
      if (isScrolling) return;

      isScrolling = true;

      window.requestAnimationFrame(() => {
        const newIndex = Math.round(slider.scrollLeft / slideWidth());

        if (newIndex !== index) {
          index = newIndex;
          updateDots();
        }

        isScrolling = false;
      });
    });

    // --- resize fix ---
    window.addEventListener('resize', () => {
      goToSlide(index, false);
    });

    // --- init ---
    goToSlide(0, false);
  });
});